<template>
    <div class="my-payee">
        <div class="fixed-content">
            <div class="search-bar">
                <div class="input-border">
                    <input type="search" placeholder="搜索..." v-model="search" @click="showCheckbox()">
                </div>
            </div>
        </div>
        <div class="all-payee">
            <better-scroll ref="payeeList" :data="payeeList" :probeType=" probeType " :listenScrollStart=" listenScrollStart "  :listenScroll=" listenScroll " @scrollStart="payeeListScrollStart"  @scroll="payeeListScroll" > 
                <div class="payee-list">
                    <template v-for="( list, section ) in payeeListSection">
                        <h4 class="division" key=" section " :data-anchor=" section " >{{section}}</h4>
                        <ul class="list" >
                            <li class="item payee-item" v-for=" ( item, index ) in  payeeListSection[section] " :key=" section + index "  >
                                <a href="javascript:;" class="btn-checkbox" :class="{ checked: checkList.indexOf( item.id ) > -1 }"  @click="payeeCheck( item.id )">
                                    <i class="icon"></i>
                                </a>
                                <a href="javascript:;" class="btn-item" @click="payeeClick( item )">
                                    <i class="logo"></i>
                                    <div class="attr">
                                        <cite>{{ item.name }} <i class="tag" v-if=" item.tranType ">{{ item.tranType }}</i></cite>
                                        <span>{{ item.bankName }}({{ item.account }})</span>
                                    </div>
                                </a>
								<a href="javascript:;" class="btn-delete" @click="payeeDelete( item.id )">
                                    <i class="icon"></i>
                                    <cite>删除</cite>
								</a>
                            </li>
                        </ul>
                    </template>
                </div>
            </better-scroll>
            <div class="shortcut" ref="shortcut">
                <ul class="list">
                    <li class="item"  :class="{ active : item == anchorActive }" v-for=" ( item, index ) in anchorList" :data-anchor="item">{{ item }}</li>
                </ul>
            </div>
        </div>
        <div class="fixed-ft">
            <a href="javascript:;" class="btn-checkbox" :class="{ checked: ( checkList.length > 0 && payeeList.length === checkList.length ) }" @click="payeeCheckAll()">
                <i class="icon"></i>
            </a>
            <a href="javascript:;" class="btn-delete" @click=" payeeMoreDelete() ">删除（{{ this.checkList.length }}）</a>
        </div>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import delegate from 'delegate-to';

import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            routerFrom: '',
            probeType: 3,
            listenScrollStart: true,
            listenScroll: true,
            payeeListScrollStartY: 0,
            payeeList: [],
            payeeListSection: [],
            anchorList: [],
            anchorMap: {},
            anchorActive: '',
            bswHeight: 0,
            bscHeight: 0,
            search: '',
            divisionHeight: 48,
            itemHeight: 108,
            anchorHeight: 32,
            btnDeleteWidth: 120,
            checkList: []
        };
    },
    created(){
        service.getPayeeList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.list;
            _temp = _temp.sort((a, b) => {
                let _x = a.cnName.substr(0, 1);
                let _y = b.cnName.substr(0, 1);
                let _t = ( _x === _y ) ? 0 : ( _x < _y ? -1 : 1 );
                return _t;
            });
            this.payeeList = _temp;
            this._parseData( this.payeeList );
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
        this.bswHeight = document.querySelector('.all-payee .better-scroll-wrapper').clientHeight;
        let _firstTouch;
        let _touch = {};
        let _anchorHeight = this.$flexible.convert( this.anchorHeight );
        this.$refs.shortcut.addEventListener('touchstart', (e) => {
            this.bscHeight = document.querySelector('.all-payee .payee-list').clientHeight;
            let _anchor = e.target.getAttribute('data-anchor');
		    _firstTouch = e.touches[0];
		    _touch.y1 = _firstTouch.pageY;
		    _touch.anchor = _anchor;
            this._scrollTo( _anchor );
        });
        this.$refs.shortcut.addEventListener('touchmove', (e) => {
            _firstTouch = e.touches[0];
		    _touch.y2 = _firstTouch.pageY;
		    let _delta = (_touch.y2 - _touch.y1) / _anchorHeight | 0;
		    let _anchor = this.anchorList[ this.anchorList.indexOf( _touch.anchor ) + _delta ];
            this._scrollTo( _anchor );
		    e.preventDefault();
		    e.stopPropagation();
        });
		let _touchDelete = {};
        document.body.addEventListener('touchstart', delegate('.payee-item', e => {
            if (e.touches.length > 0 ) {
                _touchDelete.sx = e.touches[0].clientX;
            }
        }), false);
        document.body.addEventListener('touchmove', delegate('.payee-item', e => {
            if ( document.querySelector('.my-payee').getAttribute('class').indexOf('show-checkbox') > -1 ) {
                return true;
            }
            if (e.touches.length > 0 ) {
                _touchDelete.ex = e.touches[0].clientX;
                if( _touchDelete.sx - _touchDelete.ex > 70 ) {
                    e.delegateTarget.setAttribute('class', 'item payee-item show-delete');
                } else if ( _touchDelete.ex - _touchDelete.sx > 30 ) {
                    e.delegateTarget.setAttribute('class', 'item payee-item');
                }
            }
        }), false);
    },
    computed: {
    },
    components: {
    },
    methods: {
        _anchorHeight () {
            let flexible = window['lib'].flexible;
            return ( flexible.rem / flexible.standard ) * 32;
        },
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.payeeList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        _parseData( data ) {
            let _listSection = {};
            let _anchorList = [];
            data.forEach((item, index) => {
                let _x = item.cnName.substr(0, 1).toUpperCase();
                if( !(_x in _listSection) ){
                    _listSection[ _x ] = [];
                    _anchorList.push( _x );
                }
                _listSection[ _x ].push( item );
            });
            let _y = 0;
            let _divisionHeight = this.$flexible.convert( this.divisionHeight );
            let _itemHeight = this.$flexible.convert( this.itemHeight );
            let _fix = data.length / 3000;
            for(var item in _listSection ){
                this.anchorMap[ item ] = _y;
				_y -= _divisionHeight + _listSection[item].length * _itemHeight;
                _y += _fix;
            }

            this.payeeListSection = _listSection;
            this.anchorList = _anchorList;
        },
        _hideDelete () {
            document.querySelectorAll('.payee-item.show-delete').forEach((item)=> {
                item.setAttribute('class', 'item payee-item');
            });
        },
        payeeCheck ( id ) {
            let _index = this.checkList.findIndex((item) => { return item == id });
            if( _index === -1 ) {
                this.checkList.push( id );
            } else {
                this.checkList.splice(_index, 1);
            }
        },
        payeeCheckAll () {
            let _temp = [];
            if ( this.checkList.length !== this.payeeList.length ) {
                this.payeeList.forEach((item) => {
                    _temp.push( item.id );
                });
            }
            this.checkList = _temp;
        },
        payeeClick ( data ) {
            this.$router.push({ name: 'myPayeeDetail', params: data });
        },
        payeeDelete ( id ) {
            let _index = this.payeeList.findIndex((item) => { return item.id == id });
            if( _index > -1 ) {
                this.payeeList.splice(_index, 1);
            }
            this._parseData( this.payeeList );
            this._hideDelete();
            this.$refs.payeeList.refresh();
        },
        payeeMoreDelete ( ) {
            for (let index = 0; index < this.payeeList.length; index++) {
                if ( this.checkList.findIndex((checkId) => { return checkId == this.payeeList[ index ].id }) > -1 ) {
                    this.payeeList.splice(index--, 1 );
                }
            }
            this.checkList = [];
            this._parseData( this.payeeList );
            this.$refs.payeeList.refresh();
        },
        payeeListScrollStart (data) {
            this.payeeListScrollStartY = 0;
        },
        payeeListScroll (data) {
            this._currentAnchor( data.y );
            // 滚动时，隐藏删除按钮
            if ( this.payeeListScrollStartY - data.y > 20 ) {
                this.payeeListScrollStartY = data.y;
                this._hideDelete();
            }
        },
        showCheckbox () {
            this._hideDelete();
            document.querySelector('.my-payee').setAttribute('class', 'my-payee show-checkbox');
            this.$refs.payeeList.refresh();
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }

};
</script>



<style lang="scss" scoped>

    .btn-checkbox
    {
        position: absolute;
        top: 0px;
        left: -100px;
        height: 108px;
        width: 100px;

        @include transition(all, 0.3s);
        .icon
        {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 44px;
            height: 44px;

            background-image: url('../../../assets/img/bank/my-payee/icon-btn-checkbox.png');

            @include bg-offset-center();

            @include transform(translate(-50%, -50%));
        }   

        &.checked
        {
            .icon
            {
                background-image: url('../../../assets/img/bank/my-payee/icon-btn-checkbox-checked.png');
            }   
        }
    }
    
    .my-payee
    {
        position: relative;
        padding-top: 80px;
        height: 100%;
    }
    .my-payee.show-checkbox
    {
        padding-bottom: 100px;
        .payee-list
        {
            .list
            {
                .item
                {
                    .btn-item
                    {
                        left: 100px;
                    }
                    .btn-checkbox
                    {
                        left: 0px;
                    }
                }
            }
        }

        .fixed-ft
        {
            bottom: 0px;
        }
    }
    .fixed-content
    {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 330px;
    }
    .search-bar
    {
        position: relative;
        padding: 13px 20px;
        height: 80px;
        background-color: #fff;

        @include mini-border(bottom);

        .input-border
        {
            position: relative;
            padding: 2px 5px;
            height: 54px;
            @include mini-border(all, #d9d9d9, before, 5px);
            
            input
            {
                position: relative;
                z-index: 9;
                display: block;
                padding: 10px;
                width: 100%;
                height: 50px;
                line-height: normal;
                border: none;
            }
        }
    }
    .all-payee
    {
        position: relative;
        padding-bottom: 10px;
        height: 100%;
        background-color: #fff;

        .better-scroll-wrapper
        {
            position: relative;
            height: 100%;
            overflow: hidden;
        }

        .payee-list
        {
            .division
            {
                padding-left: 20px;
                margin: 0px;
                height: 48px;
                line-height: 48px;
                background-color: #f2f2f2;
                font-size: 13px;
            }
            .list
            {
                .item
                {
                    position: relative;
                    @include mini-border(top);
                    .btn-item
					{
						position: relative;
						display: block;
						padding: 10px 15px;
						padding-left: 120px;
						height: 108px;
						overflow: hidden;

						@include mini-border(top);

                        @include transition(all, 0.3s);
						&:before
						{
							left: 20px;
						}
						

						.logo
						{
							position: absolute;
							top: 10px;
							left: 10px;
							width: 80px;
							height: 80px;

							background-image: url('../../../assets/img/icon-bank-logo.png');
							@include border-radius(70px);

							@include bg-offset-center();
						}
						
						.attr
						{
							cite,span
							{
								display: block;
							}
							cite
							{
								font-size: 13px;
								line-height: 50px;
							}
							span
							{
								color: #666;
								font-size: 11px;
							}

							.tag
							{
								padding: 2px 4px;
								margin-left: 10px;
								background-color: #9C9C9C;
								color: #fff;
								font-size: 10px;
								font-style: normal;
							}
						}
					}

					.btn-delete
					{
						position: absolute;
						top: 0px;
						right: -110px;
                        padding-top: 65px;
						height: 108px;
						width: 110px;
						text-align: center;
						background-color: #FE807E;

                        @include transition(all, 0.3s);

                        .icon
                        {
                            position: absolute;
							top: 15px;
							left: 50%;
                            margin-left: -20px;
							width: 40px;
							height: 40px;

							background-image: url('../../../assets/img/bank/my-payee/icon-btn-delete.png');

							@include bg-offset-center();
                        }
					}
                }

                .item:first-child
                {
                    a
                    {
                        &:before
                        {
                            content: none;
                        }
                    }
                }
                .item.show-delete
                {
                    .btn-item
                    {
                        @include transform(translateX(-110px));
                    }
                    .btn-delete
                    {
                        right: 0px;
                    }
                }
            }
        }
    }

    .shortcut
    {
        @include offset-center();

        left: auto;
        right: 0px;
        @include transform(translateY(-45%));

        .list
        {
            .item
            {
                height: 32px;
                line-height: 32px;
                width: 60px;
                text-align: center;
                color: #666;
                &.active
                {
                    color: #4893E3;
                }
            }
        }
    }

    .fixed-ft
    {
        position: absolute;
        bottom: -100%;
        left: 0px;
        width: 100%;
        height: 100px;
        background-color: #fff;

        @include mini-border(top);

        .btn-checkbox
        {
            left: 0px;
        }

        .btn-delete
        {
            position: absolute;
            top: 0px;
            right: 0px;
            min-width: 100px;
            height: 100px;
            line-height: 100px;
            text-align: center;
            font-size: 14px;
            color: #FF6D62;
        }
    }
    
    
</style>



